@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,700,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.alert-card {
  box-shadow: 0px 0px 13px 0px rgba(82, 63, 105, 0.1);
}
.login-content {
  height: 100vh;
  position: relative;
  background-position-y: bottom;
  background-size: cover;
  background-repeat: no-repeat;
}

.login-box {
  max-width: 500px;
  box-shadow: rgba(31, 45, 61, 0.08) 0px 0px 1.25rem;
  margin: 0;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 200px;
}

.login-form .ant-form-item-label {
  line-height: 27px;
}

.login-form .ant-form-item label {
  font-weight: 500;
  font-size: 12px;
  color: #2D2E2E;
}

.login-form .ant-form-item-control input {
  font-size: 12px;
}

.login-form .has-error .ant-form-explain, .login-form .has-error .ant-form-split {
  margin-top: 2px;
  font-size: 12px;
}

@media only screen and (max-width: 900px) {
  .login-box {
    right: 0;
    margin: 0 auto;
    position: relative;
  }
}

@media only screen and (max-width: 500px) {
  .login-box {
    width: calc(100% - 30px);
  }
}
header.haccoon-header {
  background: #FFF;
  display: table;
  position: fixed;
  top: 0;
  z-index: 10;
  padding: 0;
  width: 100%;
  height: 80px;
  line-height: 80px;
  box-shadow: 0px 0px 40px 0px rgba(82, 63, 105, 0.1) !important;
  -webkit-transition-timing-function: ease-in;
          transition-timing-function: ease-in;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

header.haccoon-header-affixed {
  background: #FFF;
  display: table;
  position: fixed;
  top: 0;
  z-index: 10;
  padding: 0;
  width: 100%;
  height: 60px;
  line-height: 60px;
  box-shadow: 0px 0px 40px 0px rgba(82, 63, 105, 0.1) !important;
  -webkit-transition: 0.25s;
  transition: 0.25s;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

.header-content {
  padding: 0 115px;
  display: flex;
  align-items: stretch;
  vertical-align: middle;
}

.header-menu {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;
}

.header-outer-options {
  display: flex;
  align-items: stretch;
  padding: 0;
  justify-content: flex-end;
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;
}

.header-menu,
.header-menu.ant-menu-horizontal > .ant-menu-item-selected, .header-menu.ant-menu-horizontal > .ant-menu-submenu-selected,
.header-menu .ant-menu-item, .header-menu .ant-menu-submenu {
  border-bottom: none !important;
}

.header-menu .ant-menu-item > a {
  padding: 0 10px;
}

.header-menu.ant-menu-horizontal > .ant-menu-item-selected a, .header-menu.ant-menu-horizontal > .ant-menu-submenu-selected {
  background: #00AD450F;
  border-radius: 4px;
  color: #00AD45;
  font-weight: 500;
}

.header-card-info .ant-card-meta-detail > div:not(:last-child) {
  margin-bottom: 0;
}

.header-card-list-options .ant-card-meta-detail > div:not(:last-child) {
  margin-bottom: 3px;
}

.header-card-list-options .ant-card-meta-title, .header-card-list-options .ant-card-meta-description {
  line-height: normal;
}

.header-card-list-options .ant-menu-item {
  height: 60px;
  line-height: 30px;
  margin: 0 !important;
  padding: 16px 14px 0 17px;
}

.header-card-list-options.ant-menu-vertical {
  border-right: none;
}

.header-card-list-options .header-card-right-icon {
  font-size: 10px;
  position: absolute;
  top: calc(50% - 3px);
  right: 7px;
  margin-right: 0;
}

.header-popover-card .ant-popover-inner-content, .header-popover-card .ant-card-body {
  padding: 0;
}

.menu-user {
  text-align: right;
}

.menu-user .ant-dropdown-trigger {
  cursor: pointer;
}

.ant-dropdown-menu-item a .anticon {
  margin-right: 8px;
}

@media only screen and (max-width: 576px) {
  .menu-user span.username, .menu-user .anticon {
    display: none !important;
  }

  .menu-user .ant-avatar {
    margin-right: 0 !important;
    height: 24px !important;
    width: 24px !important;
    margin-top: -4px !important;
  }

  .header-content div.text-center {
    text-align: left !important;
  }

  .header-content {
    padding: 0 30px;
  }
}

@media only screen and (max-width: 430px) {
  .card-width {
    width: 250px !important;
  }
}

@media only screen and (max-width: 992px) {
  .header-menu {
    display: none;
  }
}
.footer-redes-sociais:before {
  content: "";
  display: block;
  width: 80%;
  position: absolute;
  left: 50%;
  margin-left: -40%;
  height: 1px;
  background: -webkit-radial-gradient(center, ellipse, rgba(222, 228, 239, 1) 0, hsla(0, 0%, 100%, 0) 75%);
  background: radial-gradient(ellipse at center, rgba(222, 228, 239, 1) 0, hsla(0, 0%, 100%, 0) 75%);
}

.footer-redes-sociais {
  position: relative;
  border: 0;
  margin-top: 20px !important;
}
.main-content {
  padding: 104px 0 24px;
  background: #FAFCFE;
}

.main-layout-breadcrumb .ant-breadcrumb-separator {
  vertical-align: sub;
}

.main-layout-breadcrumb-separator {
  margin: 0 6px;
  display: -webkit-inline-box;
  vertical-align: middle;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: #e7e8ef;
}

.ant-layout-sider-zero-width-trigger {
  top: 100px;
}

@media only screen and (max-width: 992px) {
  .menu-breadcrumb {
    display: block !important;
  }
}

@media only screen and (min-width: 992px) {
  .mobile-menu {
    display: none;
  }
}
.img-icon {
    height: 32px;
    margin-top: -32px;
    margin-left: -16px;
}

.ant-popover {
    z-index: 999 !important;
}

.ant-card-head-wrapper {
    display: block;
}

.ant-list-item-meta-content {
    overflow: hidden;
}
.profile-form .ant-col.ant-form-item-label {
  position: absolute;
}

.profile-form .ant-form-item { 
  margin-bottom: 20px;
}
.calendar-edit-event:hover {
  color: #2F80ED;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.calendar-delete-event:hover {
  color: #FF5154;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.twitter-picker {
  box-shadow: none !important;
  border: 1px solid #d9d9d9 !important;
  border-radius: 4px !important;
  width: 100% !important;
}

.game-list-item {
  text-align: center;
}

.game-list-item .ant-avatar, .game-list-item {
  height: 125px;
  width: 100%;
  max-width: 125px;
  padding: 10px;
}
a.ant-typography-expand:after {
  content: "Expandir";
  color: #2F80ED;
  font-weight: 600;
  margin-left: -56px;
}

a.ant-typography-expand,
a.ant-typography-expand:hover {
  color: transparent;
}

.upload-list-inline .ant-upload-list-item {
  float: left;
  width: 200px;
  margin-right: 8px;
}

.upload-list-inline .ant-upload-animate-enter {
  -webkit-animation-name: uploadAnimateInlineIn;
          animation-name: uploadAnimateInlineIn;
}

.upload-list-inline .ant-upload-animate-leave {
  -webkit-animation-name: uploadAnimateInlineOut;
          animation-name: uploadAnimateInlineOut;
}

.avatar-contain img {
  object-fit: contain;
}

.modal-map {
  width: 600px !important;
}
.card-list-users-web .ant-list-item-meta-title {
  margin-bottom: 0;
}

.card-list-users-web .ant-list-item-meta-content {
  margin-top: 30px;
}

.image-carousel {
  height: 300px;
  width: 100%;
  object-fit: contain;
  border-radius: 4px;
  object-position: 50% 100%;
}

.ant-carousel .slick-slide {
  text-align: center;
  height: 300px;
}

.profile-form .ant-col.ant-form-item-label {
  position: absolute;
}

.profile-form .ant-form-item { 
  margin-bottom: 20px;
}

.tabs-card .ant-tabs-bar.ant-tabs-top-bar.ant-tabs-card-bar {
  margin-bottom: 0px;
}

.editable-row .ant-form-explain {
  position: absolute;
  font-size: 12px;
  margin-top: -4px;
}

.icon-check-color:hover {
  color: #00AD45;
}

.icon-close-color:hover {
  color: #EB3B5A;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

